<template>
  <div class="outer-container">  
  <div class="table-container">
    <el-button style="width: 100px; float: left;" type="primary" @click="showFiltDialog">筛选</el-button>
  <el-table
    :data="tableData"
    style="width: 85%"
    :row-class-name="tableRowClassName" max-height="750">
    <el-table-column
      prop="device_id"
      label="设备ID"
      width="150">
    </el-table-column>
    <el-table-column
      prop="device_name"
      label="设备名称"
      width="180">
    </el-table-column>
    <el-table-column
      prop="info"
      label="发送信息"
      width="500">
    </el-table-column>
    <el-table-column
      prop="lat"
      label="所在经度"
      width="100">
    </el-table-column>
    <el-table-column
      prop="lng"
      label="所在纬度"
      width="100">
    </el-table-column>
    <el-table-column
      prop="stamp"
      label="发送时间"
      width="200">
    </el-table-column>
    <el-table-column
      prop="value"
      label="发送的值"
      width="200">
    </el-table-column>
  </el-table>
</div>
<div>  
    <el-dialog :visible.sync="dialogVisible" title="筛选" width="30%" :show-close="false" :modal-append-to-body="false">  
      <el-form :model="filtForm" ref="filtForm" label-width="100px">  
        <el-form-item label="设备名称" prop="device_name">  
          <el-input v-model="filtForm.device_name"></el-input>  
        </el-form-item>  
        <el-form-item label="设备ID" prop="device_id">  
          <el-input v-model="filtForm.device_id"></el-input>  
        </el-form-item> 

          <el-form-item label="是否告警" >  
          <el-select  v-model="chooseOption" placeholder="请选择" style="width: 100%">  
          <el-option  
            v-for="option in selectOptions"  
            :key="option.value"  
            :label="option.label"  
            :value="option.value"  
          ></el-option>  
          </el-select>  
          </el-form-item> 

          <el-form-item label="排序方式" >  
          <el-select  v-model="chooseOrder" placeholder="请选择" style="width: 100%">  
          <el-option  
            v-for="option in selectOrders"  
            :key="option.value"  
            :label="option.label"  
            :value="option.value"  
          ></el-option>  
          </el-select>  
          </el-form-item> 

      </el-form>  
      <div slot="footer" class="dialog-footer" style="display: flex;">  
        <el-button @click="closeDialog">取消</el-button>  
        <el-button type="primary" @click="saveSelection">筛选</el-button>  
      </div>  
    </el-dialog>  
    </div>
</div>
</template>

<style>
.outer-container {  
  position: fixed;  
  top: 68px;  
  width: 100%;  
  height: 950px;  
  overflow: auto;  
  overflow-x: scroll; /* 添加此行以启用水平滚动条 */  
  overflow-y: scroll; /* 添加此行以启用垂直滚动条 */ 
}  
  
.table-container {  
  width: 100%;  
}  
  .el-table .warning-row {
    background: rgb(249, 192, 21);
  }

  .el-table .success-row {
    background: #e0e3de;
  }
</style>

<script>
import axios from 'axios'; 
import moment from 'moment'; 
  export default {
    data() {
      return {
        tableData:[],
        dialogVisible: false,
        filtForm:{
          device_name: '',
          device_id: '',
        },
        chooseOption: '',  
        chooseOrder: '',
      selectOrders: [  
        { value: '1', label: '升序' },  
        { value: '2', label: '降序'} 
      ],  
      selectOptions : [  
        { value: '1', label: '告警信息' },
        { value: '2', label: '正常信息' },
      ],  
      selectedDeviceName: '',
      selectedDeviceID: '0',
      selectedOption: '0',
      selectedOrder: '0',
      }
    },
    methods: {
      tableRowClassName({row, rowIndex}) {
        if (row.alert === 1) {
          return 'warning-row';
        } else {
          return 'success-row';
        }
      },
      fetchTableData() {  
      const params = new URLSearchParams();  
      params.append('id',this.$store.state.userID);
      params.append('device_name',this.selectedDeviceName);
      params.append('device_id', this.selectedDeviceID);
      params.append('option', this.selectedOption);
      params.append('order', this.selectedOrder);
      axios.post('/getMessage', params)
      .then(response => {  
        this.tableData = response.data.map(item =>({
          device_id: item.deviceId,
          device_name: item.deviceName,
          info: item.info,
          lat: item.lat,
          lng: item.lng,
          stamp: moment(item.stamp).format('YYYY-MM-DD HH:mm:ss'),
          value: item.value,
          alert: item.alert
        }));
      })  
      .catch(error => {  
          console.error('Error:', error);  
      }); 
      },

      showFiltDialog()
      {
        this.dialogVisible = true;
      },
    closeDialog()
    {
      this.filtForm = {
        device_name: '',
        device_id: ''
      }
      this.chooseOption = '';
      this.chooseOrder = '';
      this.dialogVisible = false;
    },
    saveSelection() { 
      this.selectedDeviceName = this.filtForm.device_name;
      if(this.filtForm.device_id === '') {this.selectedDeviceID = '0';} 
      else {this.selectedDeviceID = this.filtForm.device_id;}

      if(this.chooseOption === '') {this.selectedOption = '0';} 
      else {this.selectedOption = this.chooseOption;}

      if(this.chooseOrder === '') {this.selectedOrder = '0';} 
      else {this.selectedOrder = this.chooseOrder;}
      this.closeDialog(); 
      this.fetchTableData();
    },
    
    },
    created() {
        if (sessionStorage.getItem('store')) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem('store'))
          )
        )
      }

      // 页面刷新时，将vuex里的信息保存到sessionStorage
     // 在页面刷新时先触发beforeunload事件
      window.addEventListener('beforeunload', () => {
        sessionStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    },
    mounted()
    {
      // setInterval(this.fetchTableData, 200); 
      this.fetchTableData();
    }
  }
</script>