<template>
  <div>
  <baidu-map class="map" :center="center" :zoom="zoom"  @ready="handler"  :scroll-wheel-zoom="true">
    <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale>
    <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
    <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_LEFT"></bm-map-type>
      <bm-marker v-for="i in tableData" :key="i.id"  :position="{lng: i.lng, lat: i.lat}" :dragging="false"  :title="`${i.device_name}  ${i.stamp}` " >
      </bm-marker>
      <bm-polyline :path="polylinePath" stroke-color="blue" :stroke-opacity="0.8" :stroke-weight="3" :editing="false" @lineupdate="updatePolylinePath"></bm-polyline>
  </baidu-map>
  <div class="button"> 
    <el-input v-model="inputValue" placeholder="请输入指定的设备ID"></el-input>  
      <el-button type="primary" @click="setDID">查询轨迹</el-button>  
      <el-table
    :data="tableData"
    style="width: 100%"
    :row-class-name="tableRowClassName"
    @row-click="clicktable">
    <el-table-column
      prop="info"
      label="发送信息"
      width="250">
    </el-table-column>
    <el-table-column
      prop="stamp"
      label="发送时间"
      width="200">
    </el-table-column>
    <el-table-column
      prop="value"
      label="发送的值"
      width="100">
    </el-table-column>
  </el-table>
  <h3>仅支持查询最近10条消息记录</h3>
  </div>
</div>

</template>

<script>
import Vue from 'vue';
import BaiduMap from 'vue-baidu-map';
import axios from 'axios'; 
import moment from 'moment'; 

Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: 'ulL2XiMw8rpKVifz4SOEatf4ujKGDKb9'
})
export default{
  data () {
    return {
      center: {lng: 116.404, lat: 39.915},
      zoom: 1,
      tableData : [],
      polylinePath : [],
      D_ID: 0,
      inputValue: '',
      i: 0
    }
  },
  methods:{
    tableRowClassName({row, rowIndex}) {
        row.index = rowIndex;
        if (row.alert === 1) {
          return 'warning-row';
        } else {
          return 'success-row';
        }
      },
    clicktable(row, column, event) {  
       this.center.lat = this.polylinePath[row.index].lat;
       this.center.lng = this.polylinePath[row.index].lng;
    } ,
    updatePolylinePath (e) {
      this.polylinePath = e.target.getPath()
    },
    setDID()
    {
      if(this.inputValue === '')
      {
        alert("请输入设备ID!");
      }
      else
      {
        this.D_ID = this.inputValue;
        this.inputValue = '';
        this.fetchTableData();
      }  
    },
    handler ({BMap, map}) {
            console.log(BMap, map)
            this.center.lng = 116.404
            this.center.lat = 39.915
            this.zoom = 10
           },

    fetchTableData(){  
    const params = new URLSearchParams();  
    params.append('userid',this.$store.state.userID);
    params.append('device_id', this.D_ID);
    axios.post('/getPoints', params)
    .then(response => {  
      if(response.data.first === 0)
      {
        this.tableData = null;
        this.center.lng = 116.404
        this.center.lat = 39.915
      }
      else{
      this.tableData = response.data.second.map(item =>({
        id: item.id,
        info: item.info,
        value: item.value,
        device_name: item.deviceName,
        device_id: item.deviceId,
        lat: item.lat,
        lng: item.lng,
        stamp: moment(item.stamp).format('YYYY-MM-DD HH:mm:ss'),
        alert: item.alert,
      }));
      this.polylinePath = response.data.second.map(item =>({
        lat: item.lat,
        lng: item.lng,
      }));
      if (this.polylinePath.length > 0) {  
              this.center.lng = this.polylinePath[0].lng;  
              this.center.lat = this.polylinePath[0].lat;  
      }  
      else{
        this.center.lng = 116.404
        this.center.lat = 39.915
      }
    }})  
    .catch(error => {  
        console.error('Error:', error);  
    }); 

    }
  },
  created() {
        if (sessionStorage.getItem('store')) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem('store'))
          )
        )
      }

      // 页面刷新时，将vuex里的信息保存到sessionStorage
     // 在页面刷新时先触发beforeunload事件
      window.addEventListener('beforeunload', () => {
        sessionStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    },
  mounted()
  {
    // setInterval(this.fetchTableData, 200); 
    this.fetchTableData();
  }
}
</script>

<style>
.map {
  width: 60%;
  height: 900px;
  bottom: 0;  
  right: 0;
  transform: translateY(-25%); 
}
.button {  
  position: absolute;  
  top: 130px;  
  right: 0;  
  width: 33%;  
}  
.el-table .warning-row {
    background: rgb(249, 192, 21);
  }

  .el-table .success-row {
    background: #e0e3de;
  }
</style>