<template>
  <div id='test'>
    <v-chart class="chart" :option="option" autoresize />
  </div>
</template>

<script>
import axios from 'axios'; 
import { use } from 'echarts/core'
import { PieChart } from 'echarts/charts'
import { TooltipComponent, LegendComponent } from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'
use([TooltipComponent, LegendComponent, PieChart, CanvasRenderer])
import VChart, { THEME_KEY } from "vue-echarts";
import { ref } from "vue";
  export default {
    data() {
      return {
        type1_num : 8,
        type2_num : 8,
        type3_num : 3,
        type4_num : 2,
        type5_num : 4,
      }
    },
    components:{
      VChart,
    },
    provide: {
        // 主题颜色（white/dark）
        [THEME_KEY]: "white",
    },
  setup(){
    const option = ref({tooltip: {
    trigger: 'item'
  },
  legend: {
    top: '20%',
    left: 'center'
  },
  series: [
    {
      name: 'Access From',
      type: 'pie',
      radius: ['40%', '70%'],
      avoidLabelOverlap: false,
      itemStyle: {
        borderRadius: 10,
        borderColor: '#fff',
        borderWidth: 2
      },
      label: {
        show: false,
        position: 'center'
      },
      emphasis: {
        label: {
          show: true,
          fontSize: 40,
          fontWeight: 'bold'
        }
      },
      labelLine: {
        show: false
      },
      data: [
        { value: null, name: '车载设备' },
        { value: null, name: '智能家居' },
        { value: null, name: '可穿戴设备' },
        { value: null, name: '基础设施' },
        { value: null, name: '其他设备' }
      ]
    }
  ]});
    	return { option };
    },
    methods: {
      getData(){
      const params = new URLSearchParams();  
      params.append('id',this.$store.state.userID)
      axios.post('/mountFirst', params)  
      .then(response => {  
        this.option.series[0].data[0].value = response.data[0];
        this.option.series[0].data[1].value = response.data[1];
        this.option.series[0].data[2].value = response.data[2];
        this.option.series[0].data[3].value = response.data[3];
        this.option.series[0].data[4].value = response.data[4];
      })  
      .catch(error => {  
          console.error('Error:', error);  
      }); 
      }
    },
    created() {
        if (sessionStorage.getItem('store')) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem('store'))
          )
        )
      }

      // 页面刷新时，将vuex里的信息保存到sessionStorage
     // 在页面刷新时先触发beforeunload事件
      window.addEventListener('beforeunload', () => {
        sessionStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    },
    mounted() {
      // setInterval(this.getData, 200); 
      this.getData();
    },
  }
</script>

<style lang='less' scope>
  #test{
    width: 100%;
    height: 100vh;
    
  }
</style>
