<template>
  <div class="outer-container">  
  <div class="table-container">
    <el-button style="width: 200px; margin-bottom: 10px; float:left; " type="primary" @click="showAdderDialog">+添加设备</el-button>
    <el-table
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 85%" :row-class-name="tableRowClassName" max-height="750">
    <el-table-column
      label="设备ID"
      prop="id"
      width="100">
    </el-table-column>
    <el-table-column
      label="设备名"
      prop="name"
      width="150">
    </el-table-column>
    <el-table-column
      label="设备描述"
      prop="description"
      width="600">
    </el-table-column>
    <el-table-column
      label="设备类型"
      prop="kind"
      width="200">
      <template v-slot:default="scope">
        {{ scope.row.kind === 1 ? '车载设备' : (scope.row.kind === 2 ? '智能家居' : (scope.row.kind === 3 ? '可穿戴设备' : (scope.row.kind === 4 ? '基础设施' : '其他设备'))) }}
      </template>
    </el-table-column>
    <el-table-column
      label="在线情况"
      prop="activate"
      width="150">
      <template v-slot:default="scope">
      {{ scope.row.activate === 'now' ? '在线' :  '离线' }}
    </template>
    </el-table-column>
    <el-table-column>
      <template v-slot:header="">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入设备名关键词"/>
      </template>
      <template slot-scope="scope">
        <div class="button-container">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑设备</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除设备</el-button>
        </div> 
      </template>
    </el-table-column>
  </el-table>

  <div>  
    <el-dialog :visible.sync="editDialogVisible" title="修改" width="30%" :show-close="false" :modal-append-to-body="false">  
      <el-form :model="editForm" ref="editForm" label-width="100px">  
        <el-form-item label="设备名称" prop="device_name">  
          <el-input v-model="editForm.device_name"></el-input>  
        </el-form-item>  
        <el-form-item label="设备描述" prop="device_name">  
          <el-input v-model="editForm.device_des"></el-input>  
        </el-form-item>  
          <el-form-item label="设备类型" >  
          <el-select  v-model="chooseOption" placeholder="请选择" style="width: 100%">  
          <el-option  
            v-for="option in selectOptions"  
            :key="option.value"  
            :label="option.label"  
            :value="option.value"  
          ></el-option>  
          </el-select>  
          </el-form-item> 

      </el-form>  
      <div slot="footer" class="dialog-footer" style="display: flex;">  
        <el-button @click="closeDialog">取消</el-button>  
        <el-button type="primary" @click="saveSelection">修改</el-button>  
      </div>  
    </el-dialog>  
    </div>



    <div>  
    <el-dialog :visible.sync="delDialogVisible" title="确定要删除该设备吗？" width="60%" :show-close="false" :modal-append-to-body="false">  
      <div slot="footer" class="dialog-footer" style="display: flex;">  
        <el-button @click="closedelDialog">取消</el-button>  
        <el-button type="primary" @click="deleteDevice">删除</el-button>  
      </div>  
    </el-dialog>  
    </div>

  <div>
    <el-dialog :visible.sync="AdddialogVisible" title="添加设备" width="30%" :show-close="false" :modal-append-to-body="false">  
      <el-form :model="AdderForm" ref="AdderForm" label-width="100px">  
        <el-form-item label="设备名称" >  
          <el-input v-model="AdderForm.device_name"></el-input>  
        </el-form-item>  

          <el-form-item label="设备类型" >  
          <el-select  v-model="chooseType" placeholder="请选择" style="width: 100%">  
          <el-option  
            v-for="option in TypeChoices"  
            :key="option.value"  
            :label="option.label"  
            :value="option.value"  
          ></el-option>  
          </el-select>  
          </el-form-item> 

        <el-form-item label="设备描述" >
        <el-input type="textarea" v-model="AdderForm.desc"></el-input>
      </el-form-item>

      </el-form>  
      <div slot="footer" class="dialog-footer" style="display: flex;">  
        <el-button @click="closeAddDialog">取消</el-button>  
        <el-button type="primary" @click="saveAdder">添加</el-button>  
        </div>
      </el-dialog>  
      </div>  
  </div>
  </div>
</template>



<script>
import axios from 'axios';  
export default {
    data() {
        return {
        AdddialogVisible: false,
        AdderForm:{
          device_name:'',
          desc:''
        },
        chooseType:'',
        selectedName:'',
        selectedType:'',
        selectedDes:'',
        TypeChoices:
        [
        { value: "1", label: '车载设备' },
        { value: "2", label: '智能家居' },
        { value: "3", label: '可穿戴设备' },
        { value: "4", label: '基础设施' },
        { value: "5", label: '其他设备' },
        ],

        tableData: [],
        selectedID: 0,
        delselectedID: 0,
        search: '',
        editDialogVisible: false,
        delDialogVisible: false,
        editForm:{
          device_name : '',
          device_des : ''
        },
        chooseOption: '', 
        selectOptions:
        [
        { value: "1", label: '车载设备' },
        { value: "2", label: '智能家居' },
        { value: "3", label: '可穿戴设备' },
        { value: "4", label: '基础设施' },
        { value: "5", label: '其他设备' },
        ],
        selectedDeviceName: '',
        selectedDeviceDesc:'',
        selectedOption: "0",
      }
    },
    methods: {
      handleEdit(index, row) {
        this.editDialogVisible = true;
        this.selectedID = row.id;
        this.fetchTableData();
      },
      handleDelete(index, row) {
        this.delDialogVisible = true;
        this.delselectedID = row.id;
        this.fetchTableData();
      },
    fetchTableData(){  
    const params = new URLSearchParams();  
    params.append('userid',this.$store.state.userID);
    axios.post('/getUserDevice', params)
    .then(response => {  
      this.tableData = response.data.map(item =>({
       id: item.id,
       name: item.name,
       description: item.description,
       kind: item.kind,
       activate: item.activate
      }));
    })  
    .catch(error => {  
        console.error('Error:', error);  
    }); 
    },
    editData()
    {
      const params = new URLSearchParams(); 
      params.append('device_id',this.selectedID);
      params.append('device_type',this.selectedOption);
      params.append('device_name',this.selectedDeviceName);
      params.append('device_desc',this.selectedDeviceDesc);
      axios.post('/editDevice', params)
      .then(response => {  
        if(response.data === 1)
        {
          alert("修改成功");
          this.closeDialog();
          this.fetchTableData();
        }
        else 
        {
          alert("重复的设备名！");
          this.closeDialog();
          this.fetchTableData();
        }
    })  
    .catch(error => {  
        console.error('Error:', error);  
    }); 
    },
    closeDialog()
    {
      this.editForm = {device_name: ''},
      this.chooseOption = '',
      this.editDialogVisible = false;
    },
    saveSelection()
    {
      if(this.chooseOption === ''){this.selectedOption = "0";}
      else {this.selectedOption = this.chooseOption;}
      this.selectedDeviceName = this.editForm.device_name;
      this.selectedDeviceDesc = this.editForm.device_des;
      this.editData();
    },
    closedelDialog()
    {
      this.delDialogVisible = false;
    },
    deleteDevice()
    {
      const params = new URLSearchParams(); 
      params.append('device_id',this.delselectedID);
      axios.post('/delDevice', params)
      .then(response => {  
        if(response.data === 1)
        {
          alert("删除成功");
          this.closedelDialog();
          this.fetchTableData();
        }
        else 
        {
          alert("删除失败！");
          this.closedelDialog();
          this.fetchTableData();
        }
    })  
    .catch(error => {  
        console.error('Error:', error);  
    }); 
    },
    AddData()
    {
      const params = new URLSearchParams();  
      params.append('user_id',this.$store.state.userID);
      params.append('device_name',this.selectedName);
      params.append('device_type',this.selectedType);
      params.append('device_des',this.selectedDes);
      axios.post('/Adder', params)
      .then(response => {  
        if(response.data === 1)
        {
          alert("添加成功");
          this.closeAddDialog();
          this.fetchTableData();
          this.fetchTableData();
        }
        else 
        {
          alert("重复的设备名！");
          this.closeAddDialog();
          this.fetchTableData();
        }
      })  
      .catch(error => {  
          console.error('Error:', error);  
      }); 
    },
    tableRowClassName({row, rowIndex}) {
        if (row.activate === "now") {
          return 'online-row';
        } else {
          return 'offline-row';
        }
      },
      showAdderDialog()
      {
        this.AdddialogVisible = true;
      },
      closeAddDialog()
      {
        this.chooseType = '';
        this.AdderForm = {
          device_name:'',
          desc:''
        }
        this.AdddialogVisible = false;
      },
      saveAdder()
      {
        if((this.chooseType === '') || (this.AdderForm.device_name === '')||(this.AdderForm.desc === ''))
        {
          alert("请输入完整的信息！");
        }
        else{
          this.selectedType = this.chooseType;
          this.selectedName = this.AdderForm.device_name;
          this.selectedDes = this.AdderForm.desc;
          this.AddData();
        }
      }
    },
  mounted()
  {
    // setInterval(this.fetchTableData, 200); 
    this.fetchTableData();
  },
  created() {
        if (sessionStorage.getItem('store')) {
        this.$store.replaceState(
          Object.assign(
            {},
            this.$store.state,
            JSON.parse(sessionStorage.getItem('store'))
          )
        )
      }

      // 页面刷新时，将vuex里的信息保存到sessionStorage
     // 在页面刷新时先触发beforeunload事件
      window.addEventListener('beforeunload', () => {
        sessionStorage.setItem('store', JSON.stringify(this.$store.state))
      })
    },

    
}
</script>

<style>
.outer-container {   
  position: fixed;  
  top: 68px;  
  width: 100%;  
  height: 950px;  
  overflow: auto;  
  overflow-y: auto;
}  
  
.table-container {  
  display: flex;  
  flex-direction: column;  
  width: 100%;  
}  
.button-container {  
  display: flex;   
  justify-content: space-between;  
}  
.el-table .online-row {
    background: rgb(204, 235, 158);
  }

  .el-table .offline-row {
    background: white;
  }
</style>