import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count : 0,
    currentID: -1, //name
    userID: -1 //id
  },
  mutations:{
    increment(state, value)
    {
      state.count = state.count + value;
    },
    setCurrentID(state, value)
    {
      state.currentID = value;
    },
    setUID(state, value)
    {
      state.userID = value;
    },
    cleanCurrentID(state)
    {
      state.currentID = -1;
    },
    cleanUID(state)
    {
      state.userID = -1;
    }
  }
})

export default store 